<template>
  <el-drawer
    v-model="visible"
    :title="form.id === 0 ? '新增主体' : '编辑主体'"
    direction="rtl"
    size="60%"
    :close-on-click-modal="false"
  >
    <el-form :model="form" label-width="140px" :rules="rules" ref="formRef">
      <!-- 代理商选择（仅管理员） -->
      <el-row :gutter="20" v-if="!isAgent">
        <el-col :span="24">
          <el-form-item label="代理商" prop="agent_id">
            <el-select 
              v-model="form.agent_id" 
              placeholder="请选择代理商" 
              clearable
              filterable
              :disabled="form.id > 0"
              @change="handleAgentChange"
            >
              <el-option
                v-for="agent in agentList"
                :key="agent.id"
                :label="agent.agent_name"
                :value="agent.id"
              />
            </el-select>
            <div class="form-tip" v-if="form.id > 0">代理商创建后不可修改</div>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="企业名称" prop="company_name">
            <el-input 
              v-model="form.company_name" 
              placeholder="请输入企业名称" 
              clearable
            />
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="24">
          <el-form-item label="分账方式" prop="royalty_type">
            <el-select 
              v-model="form.royalty_type" 
              placeholder="请选择分账方式"
            >
              <el-option label="不分账" value="none" />
              <el-option label="单笔" value="single" />
              <el-option label="商家（暂不支持）" value="merchant" :disabled="true" />
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20" v-if="form.royalty_type === 'single' || form.royalty_type === 'merchant'">
        <el-col :span="12">
          <el-form-item label="分账模式" prop="royalty_mode">
            <el-select 
              v-model="form.royalty_mode" 
              placeholder="请选择分账模式"
            >
              <el-option label="普通" value="normal" />
              <el-option label="主子（暂不支持）" value="master_sub" :disabled="true" />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="分账比例" prop="royalty_rate">
            <el-input 
              v-model.number="form.royalty_rate" 
              placeholder="请输入分账比例"
              clearable
            >
              <template #append>%</template>
            </el-input>
            <div class="form-tip">输入数字，如：10.5 表示 10.5%</div>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item label="异地拉单" prop="allow_remote_order">
            <el-switch 
              v-model="form.allow_remote_order" 
              :active-value="1" 
              :inactive-value="0"
              active-text="支持"
              inactive-text="不支持"
            />
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="验证设备" prop="verify_device">
            <el-switch 
              v-model="form.verify_device" 
              :active-value="1" 
              :inactive-value="0"
              active-text="是"
              inactive-text="否"
            />
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="扫码支付" prop="scan_pay_enabled">
            <el-switch 
              v-model="form.scan_pay_enabled" 
              :active-value="1" 
              :inactive-value="0"
              active-text="开启"
              inactive-text="关闭"
            />
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item label="交易笔数限制" prop="transaction_limit">
            <el-input 
              v-model.number="form.transaction_limit" 
              placeholder="请输入每日交易笔数限制"
              clearable
              type="number"
            >
              <template #append>笔/天</template>
            </el-input>
            <div class="form-tip">留空表示不限制</div>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="最小交易金额" prop="amount_min">
            <el-input 
              v-model.number="form.amount_min" 
              placeholder="请输入单笔最小金额"
              clearable
              type="number"
            >
              <template #append>元</template>
            </el-input>
            <div class="form-tip">留空表示不限制</div>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="最大交易金额" prop="amount_max">
            <el-input 
              v-model.number="form.amount_max" 
              placeholder="请输入单笔最大金额"
              clearable
              type="number"
            >
              <template #append>元</template>
            </el-input>
            <div class="form-tip">留空表示不限制</div>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="支付宝APPID" prop="alipay_app_id">
            <el-input 
              v-model="form.alipay_app_id" 
              placeholder="请输入支付宝应用APPID" 
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="合作伙伴ID(PID)" prop="alipay_pid">
            <el-input 
              v-model="form.alipay_pid" 
              placeholder="请输入支付宝合作伙伴ID" 
              clearable
            />
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="24">
          <el-form-item label="自定义商品标题" prop="custom_product_title">
            <el-input 
              v-model="form.custom_product_title" 
              placeholder="请输入自定义商品标题，用于支付页面显示" 
              clearable
              maxlength="50"
              show-word-limit
            />
            <div class="form-tip">支付页面显示的商品标题，留空则使用系统设定的商品名称</div>
          </el-form-item>
        </el-col>
      </el-row>

      <el-divider content-position="left">证书配置（仅支持证书模式）</el-divider>

      <el-row :gutter="20">
        <el-col :span="24">
          <el-form-item label="应用私钥" prop="app_private_key">
            <el-input 
              v-model="form.app_private_key" 
              type="textarea"
              :rows="8"
              placeholder="请输入应用私钥（RSA私钥）"
            />
            <div class="form-tip">请输入完整的应用私钥内容</div>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="24">
          <el-form-item label="应用公钥证书" prop="app_public_cert">
            <el-upload
              class="upload-cert"
              :action="uploadUrl"
              :headers="uploadHeaders"
              :before-upload="beforeUpload"
              :on-success="(response, file) => handleUploadSuccess(response, file, 'app_cert_public_key')"
              :on-error="handleUploadError"
              :show-file-list="false"
              accept=".pem,.crt,.txt"
            >
              <el-button type="primary" size="small">
                <span v-if="!form.app_cert_public_key_path">上传应用公钥证书</span>
                <span v-else>重新上传</span>
              </el-button>
            </el-upload>
            <div class="form-tip" v-if="form.app_cert_public_key_path" style="margin-top: 10px;">
              <el-tag type="success">✓ 已上传: {{ getFileName(form.app_cert_public_key_path) }}</el-tag>
            </div>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="24">
          <el-form-item label="支付宝公钥证书" prop="alipay_public_cert">
            <el-upload
              class="upload-cert"
              :action="uploadUrl"
              :headers="uploadHeaders"
              :before-upload="beforeUpload"
              :on-success="(response, file) => handleUploadSuccess(response, file, 'alipay_cert_public_key')"
              :on-error="handleUploadError"
              :show-file-list="false"
              accept=".pem,.crt,.txt"
            >
              <el-button type="primary" size="small">
                <span v-if="!form.alipay_cert_public_key_path">上传支付宝公钥证书</span>
                <span v-else>重新上传</span>
              </el-button>
            </el-upload>
            <div class="form-tip" v-if="form.alipay_cert_public_key_path" style="margin-top: 10px;">
              <el-tag type="success">✓ 已上传: {{ getFileName(form.alipay_cert_public_key_path) }}</el-tag>
            </div>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="24">
          <el-form-item label="支付宝根证书" prop="alipay_root_cert">
            <el-upload
              class="upload-cert"
              :action="uploadUrl"
              :headers="uploadHeaders"
              :before-upload="beforeUpload"
              :on-success="(response, file) => handleUploadSuccess(response, file, 'alipay_root_cert')"
              :on-error="handleUploadError"
              :show-file-list="false"
              accept=".pem,.crt,.txt"
            >
              <el-button type="primary" size="small">
                <span v-if="!form.alipay_root_cert_path">上传支付宝根证书</span>
                <span v-else>重新上传</span>
              </el-button>
            </el-upload>
            <div class="form-tip" v-if="form.alipay_root_cert_path" style="margin-top: 10px;">
              <el-tag type="success">✓ 已上传: {{ getFileName(form.alipay_root_cert_path) }}</el-tag>
            </div>
          </el-form-item>
        </el-col>
      </el-row>

      <el-form-item label="状态" prop="status">
        <el-switch 
          v-model="form.status" 
          :active-value="1" 
          :inactive-value="0"
        />
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="handleSubmit" :icon="Check">提交</el-button>
        <el-button @click="visible = false" :icon="Close">取消</el-button>
      </el-form-item>
    </el-form>
  </el-drawer>
</template>

<script lang="ts" setup>
import { ref, computed } from 'vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import { Check, Close } from '@element-plus/icons-vue';
import request from '@/utils/request';

// 定义emit
const emit = defineEmits(['refresh']);

// 表单ref
const formRef = ref();

// 控制drawer显示
const visible = ref(false);

// 表单数据
const form = ref<any>({
  id: 0,
  agent_id: '',
  product_id: '',
  royalty_type: 'none',
  royalty_mode: 'normal',
  royalty_rate: null,
  allow_remote_order: 1,
  verify_device: 0,
  scan_pay_enabled: 1,
  transaction_limit: null,
  amount_min: null,
  amount_max: null,
  company_name: '',
  alipay_app_id: '',
  alipay_pid: '',
  custom_product_title: '',
  app_private_key: '',
  app_cert_public_key: '',
  app_cert_public_key_path: '',
  alipay_cert_public_key: '',
  alipay_cert_public_key_path: '',
  alipay_root_cert: '',
  alipay_root_cert_path: '',
  status: 1
});

// 用户信息
const userInfo = ref<any>({});
const isAgent = computed(() => userInfo.value?.is_agent === true);

// 代理商列表
const agentList = ref<any[]>([]);

// 产品列表
const productList = ref<any[]>([]);


// 上传地址和header（使用与request相同的baseURL逻辑）
const uploadUrl = computed(() => {
  const baseURL = import.meta.env.DEV 
    ? '/sys'  // 开发环境使用代理
    : 'https://3pay.api.baiyi-pay.com';  // 生产环境线上接口地址
  return `${baseURL}/api/v1/admin/upload/cert`;
});
const uploadHeaders = ref({
  'Authorization': `Bearer ${localStorage.getItem('token') || ''}`
});

// 验证规则（动态）
const rules = computed(() => {
  const baseRules: any = {
    royalty_type: [
      { required: true, message: '请选择分账方式', trigger: 'change' }
    ],
    company_name: [
      { required: true, message: '请输入企业名称', trigger: 'blur' }
    ],
    alipay_app_id: [
      { required: true, message: '请输入支付宝APPID', trigger: 'blur' },
      { pattern: /^\d{16}$/, message: 'APPID格式不正确，应为16位数字', trigger: 'blur' }
    ],
    alipay_pid: [
      { required: true, message: '请输入支付宝合作伙伴ID', trigger: 'blur' },
      { pattern: /^\d{16}$/, message: 'PID格式不正确，应为16位数字', trigger: 'blur' }
    ]
  };
  
  // 只有管理员需要验证agent_id
  if (!isAgent.value) {
    baseRules.agent_id = [
      { required: true, message: '请选择代理商', trigger: 'change' }
    ];
  }
  
  // 当分账方式为单笔或商家时，分账模式和分账比例必填
  if (form.value.royalty_type === 'single' || form.value.royalty_type === 'merchant') {
    baseRules.royalty_mode = [
      { required: true, message: '请选择分账模式', trigger: 'change' }
    ];
    baseRules.royalty_rate = [
      { required: true, message: '请输入分账比例', trigger: 'blur' },
      { type: 'number', message: '分账比例必须是数字', trigger: 'blur' },
      { validator: (_rule: any, value: any, callback: any) => {
          if (value !== null && value !== undefined && value !== '') {
            if (value < 0 || value > 100) {
              callback(new Error('分账比例必须在0-100之间'));
            } else {
              callback();
            }
          } else {
            callback();
          }
        }, trigger: 'blur' 
      }
    ];
  }
  
  return baseRules;
});

// 获取用户信息
const getUserInfo = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/user/info',
      method: 'GET'
    });
    if (res.status) {
      userInfo.value = res.data;
    }
  } catch (error) {
    console.error('获取用户信息失败:', error);
  }
};

// 获取代理商列表
const getAgentList = async () => {
  if (isAgent.value) return;
  
  try {
    const res = await request({
      url: '/api/v1/admin/subject/agent-list',
      method: 'GET'
    });
    if (res.status) {
      agentList.value = res.data;
    }
  } catch (error) {
    console.error('获取代理商列表失败:', error);
  }
};

// 获取产品列表（根据代理商筛选）
const getProductList = async (agentId?: number) => {
  try {
    const params: any = {};
    if (agentId) {
      params.agent_id = agentId;
    }
    
    const res = await request({
      url: '/api/v1/admin/subject/product-list',
      method: 'GET',
      params
    });
    if (res.status) {
      productList.value = res.data;
    }
  } catch (error) {
    console.error('获取产品列表失败:', error);
  }
};

// 代理商变化时重新加载产品列表（仅管理员）
const handleAgentChange = async (agentId: number) => {
  if (agentId) {
    await getProductList(agentId);
    // 只在新增模式下清空产品选择
    if (form.value.id === 0) {
      form.value.product_id = '';
    }
  } else {
    productList.value = [];
    form.value.product_id = '';
  }
};

// 打开drawer
const open = async (row: any) => {
  // 先获取用户信息
  await getUserInfo();
  
  // 然后获取其他数据
  await getAgentList();
  
  // 加载产品列表
  if (isAgent.value && userInfo.value.agent_id) {
    await getProductList(userInfo.value.agent_id);
  } else if (row.agent_id) {
    await getProductList(row.agent_id);
  }
  
  if (row.id) {
    // 编辑模式 - 获取详情
    try {
      const res = await request({
        url: `/api/v1/admin/subject/detail/${row.id}`,
        method: 'GET'
      });
      if (res.status) {
        console.log('编辑数据加载:', res.data);
        form.value = { ...res.data };
        console.log('表单数据设置后:', form.value);
      }
    } catch (error: any) {
      console.error('获取详情失败:', error);
      const errorMessage = error.response?.data?.message || 
                          error.response?.data?.msg || 
                          error.message || 
                          '获取详情失败';
      
      // 检查是否是主体不存在的错误
      if (errorMessage.includes('主体不存在') || errorMessage.includes('请刷新列表')) {
        ElMessageBox.alert(errorMessage, '提示', {
          confirmButtonText: '刷新列表',
          type: 'warning',
          callback: () => {
            visible.value = false;
            emit('refresh'); // 刷新列表
          }
        });
      } else {
        ElMessage.error(errorMessage);
      }
      return;
    }
  } else {
    // 新增模式 - 重置表单
    const currentAgentId = isAgent.value ? userInfo.value.agent_id : '';
    
    form.value = {
      id: 0,
      agent_id: currentAgentId,  // 代理商自动填充agent_id
      product_id: '',
      royalty_type: 'none',
      royalty_mode: 'normal',
      royalty_rate: null,
      allow_remote_order: 1,
      verify_device: 0,
      scan_pay_enabled: 1,
      transaction_limit: null,
      amount_min: null,
      amount_max: null,
      company_name: '',
      alipay_app_id: '',
      alipay_pid: '',
      custom_product_title: '',
      app_private_key: '',
      app_cert_public_key: '',
      app_cert_public_key_path: '',
      alipay_cert_public_key: '',
      alipay_cert_public_key_path: '',
      alipay_root_cert: '',
      alipay_root_cert_path: '',
      status: 1
    };
    
    // 打印调试信息
    console.log('Subject - isAgent:', isAgent.value);
    console.log('Subject - userInfo:', userInfo.value);
    console.log('Subject - agent_id set to:', currentAgentId);
  }
  
  visible.value = true;
  
  // 清除验证
  setTimeout(() => {
    formRef.value?.clearValidate();
  }, 100);
};

// 提交表单
const handleSubmit = async () => {
  // 验证表单
  const valid = await formRef.value.validate().catch(() => false);
  if (!valid) {
    return;
  }

  try {
    const url = form.value.id > 0 
      ? '/api/v1/admin/subject/edit' 
      : '/api/v1/admin/subject/add';
    
    const res = await request({
      url,
      method: 'POST',
      data: form.value
    });

    if (res.status) {
      ElMessage.success((res as any).msg || '操作成功');
      visible.value = false;
      emit('refresh');
    } else {
      ElMessage.error((res as any).msg || '操作失败');
    }
  } catch (error: any) {
    console.error('提交失败:', error);
    ElMessage.error(error.response?.data?.msg || '提交失败');
  }
};

// 上传前验证
const beforeUpload = (file: any) => {
  const isLt2M = file.size / 1024 / 1024 < 2;
  if (!isLt2M) {
    ElMessage.error('文件大小不能超过 2MB!');
  }
  return isLt2M;
};

// 上传成功处理
const handleUploadSuccess = (response: any, _file: any, fieldName: string) => {
  console.log('上传响应:', response);
  if (response.status) {
    // 设置文件内容和路径
    (form.value as any)[fieldName] = response.data.content;
    (form.value as any)[`${fieldName}_path`] = response.data.path;
    ElMessage.success('上传成功');
  } else {
    console.error('上传失败响应:', response);
    ElMessage.error(response.message || response.msg || '上传失败');
  }
};

// 上传失败处理
const handleUploadError = (error: any) => {
  console.error('上传失败详情:', error);
  const errorMessage = error.response?.data?.message || 
                      error.response?.data?.msg || 
                      error.message || 
                      '上传失败，请重试';
  ElMessage.error(errorMessage);
};

// 获取文件名
const getFileName = (path: string) => {
  if (!path) return '';
  return path.split('/').pop() || '';
};

// 暴露方法给父组件
defineExpose({
  open
});
</script>

<style scoped>
.form-tip {
  font-size: 12px;
  color: #909399;
  margin-top: 4px;
}

:deep(.el-drawer__body) {
  padding: 20px;
}

.upload-cert {
  display: inline-block;
  margin-right: 10px;
}
</style>

